import React, { useEffect, useState } from 'react'
import axios from 'axios'

export default function Quote({id}) {
  const [data,setQuote]=useState([]);

  useEffect(()=>{
    axios.get('https://majestic-trouble-production.up.railway.app/rodrick/getQuote/'+id)
    .then(response=>{
      setQuote(response.data)
    });
  });

  return (
    <section className='site-section quote mt-5'>
      <div className='container-fluid row'>
        <h1>QUOTE</h1>
        <p className='fs-1 font-monospace lh-base'>{data['quote']} - {data['author']} -</p>
      </div>
      </section>
  );
}
