import React, { useState } from 'react'

export default function Landingpage() {
  const [data,setEnquiries]=useState({});

  const handleSubmit = async (event) => {
    event.preventDefault(); // prevent the default form submission behavior

    await fetch('https://majestic-trouble-production.up.railway.app/rodrick/enquiries',{
      method:'POST',
      headers:{
        'Accept':'application/json',
        'Content-Type':'application/json'
      },
      body:JSON.stringify(data),
    })
    .then(response=>{
      console.log(response.json());
    })
    .catch(error=>{
      console.error(error);
    });
    window.location.reload();
  }

  const handleChange = (event) => {
    // update the form data state whenever a form field changes
    setEnquiries({ ...data, [event.target.name]: event.target.value });
  }

  return (
    <div className='container-fluid center bg-dark pt-4 pb-2 page' id='top'>
        <div className='row mb-3'>
        <div className='row'>
                <h1>RODRICK GULU JONATHAN</h1>
            </div>
            <div className='row mb-4 mt-4'>
                <h1 className='p font-monospace'><b>"Coding is all about Passion"</b></h1>
                </div>
        <div className="row">
          <h2><i>FULLSTACK WEB DEVELOPER</i></h2>
        </div>
        <div className='row'>
            <h3><i>REACT.JS</i></h3>
        </div>
        <div className='row'>
            <h3><i>SPRINGBOOT</i></h3>
        </div>
        <div className='row'>
            <h3><i>DATABASE (MYSQL)</i></h3>
        </div>
        <div className='row'>
            <h2>JAVA PROGRAMMING</h2>
        </div>
        <div className="row m-1">
          <form className='mt-4 p' onSubmit={handleSubmit} method='POST'>
            <div className="form-floating mb-3 mt-3">
              <textarea 
              type="text" 
              name="description" 
              className="form-control" 
              placeholder="Description" 
              onChange={handleChange}
              />
              <label htmlFor="description">How may I help you?</label>
            </div>
            <div className='form-floating mb-3 mt-3 bg-light rounded'>
                <input 
                type='email' 
                className='form-control' 
                placeholder="email" 
                name="email"
                onChange={handleChange}/>
                <label htmlFor='email'>Enter your E-mail for follow up</label>
            </div>
            <button type="submit" className="btn btn-lg btn-outline-light l"><b>SUBMIT</b></button>
          </form>
        </div>
        </div>
    </div>
  );
}
