import React from 'react'
import Htmllogo from '../logos/html5-logo-31819.png'
import Javalogo from '../logos/Java_logo_icon.png'
import Mysqllogo from '../logos/MySQL-Logo.wine.png'
import Springbootlogo from '../logos/pngaaa.com-2459546.png'
import Reactlogo from '../logos/pngwing.com.png'

export default function Skills() {
  return (
    <section className='site-section mt-5' id='skills'>
          <div className='container-fluid'>
             <h1>My SKILLS AND EXPERTISE</h1>
             <div className='card-group'>
                <div className='card m-3 border-0'>
                  <img className='card-img-top img-fluid' src={Javalogo} alt='Card cap'/>
                  <div className='card-body'>
                    <h5 className='card-title'>Java Development</h5>
                    <p className='card-text'>Transforming complex problems into elegant solutions with the power of Java – that's what I do as a Java developer.</p>
                    <p className='card-text'>From designing and implementing scalable applications to fine-tuning algorithms, I love crafting code that not only meets client needs but exceeds their expectations</p>
                    <p className='card-text'>With Java as my weapon of choice, I'm always ready to take on new challenges and create impactful digital experiences</p>
                    <p className='card-text'>So, let's bring your ideas to life with Java development!</p>
                  </div>
                </div>
                <div className='card m-3 border-0'>
                  <img className='card-img-top img-fluid' src={Springbootlogo} alt='card cap'></img>
                  <div className='card-body'>
                    <h5 className='card-title'>SpringBoot Development</h5>
                    <p className='card-text'>I'm a Spring Boot developer, and I love building scalable and robust applications that run like a well-oiled machine.</p>
                    <p className='card-text'>I specialize in crafting high-quality, responsive web applications that can handle anything you throw at them</p>
                    <p className='card-text'>I have years of experience building complex, scalable systems using the Spring framework, and I'm eager to take on your next challenge. Let's build something great together!</p>
                    <p className='card-text'>With expertise in both front-end and back-end development, I can ensure your project is fast, reliable, and intuitive for users</p>
                  </div>
                </div>
                </div>

                <div className='card-group mt-3'>
                <div className='card m-2 border-0'>
                  <img className='card-img-top img-fluid' src={Htmllogo} alt='card cap'></img>
                  <div className='card-body'>
                    <h5 className='card-title'>FullStack Developer</h5>
                    <p className='card-text'>I am an xperienced full-stack web developer with a passion for creating responsive, user-friendly web applications using a range of technologies including HTML, CSS, JavaScript, Node.js, React, and MySQL.</p>
                    <p className='card-text'>Whether it's building a new website from scratch, revamping an existing one, or developing a custom web application, I take pride in delivering high-quality work that exceeds expectations</p>
                    <p className='card-text'>With strong communication skills and a collaborative mindset, I work closely with clients to ensure their vision is brought to life in the most effective and efficient way possible</p>
                  </div>
                </div>
                <div className='card m-2 border-0'>
                  <img className='card-img-top img-fluid' src={Reactlogo} alt='card cap'></img>
                  <div className='card-body'>
                    <h5 className='card-title'>Front-End Developer</h5>
                    <p className='card-text'>For Front-End evelopment, I specialized in React JS.</p>
                    <p className='card-text'>With strong communication skills and a collaborative mindset, I work closely with clients to ensure their vision is brought to life in the most effective and efficient way possible</p>
                  </div>
                </div>
                <div className='card m-2 border-0'>
                  <img className='card-img-top img-fluid' src={Mysqllogo} alt='card cap'></img>
                  <div className='card-body'>
                    <h5 className='card-title'>Database Management</h5>
                    <p className='card-text'>I also specialize in creating and maintaining high-performance database systems that help organizations efficiently manage and process their data</p>
                    <p className='card-text'>With my expertise in SQL and database design principles, I can help ensure that your database is optimized for performance, scalability, and security</p>
                    <p className='card-text'>Whether you need to manage large amounts of data, perform complex queries, or build custom database solutions, I have the skills and experience to deliver top-quality results</p>
                  </div>
                </div>
             </div>
          </div>
        </section>
  );
}
